import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import BreadCrumb from "../components/common/breadcrumb";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import TonWalletScreen from "../screens/ton-wallet";

const TonWalletPage = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Ton wallet Development Company", link: null },
  ];

  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <main>
        <TonWalletScreen db={db} />
      </main>
    </Layout>
  );
};

export const Head = () => (
  <Seo
    title="TON-Wallet-Development-Company | Justtry Technologies"
    headTitle="TON-Wallet-Development-Company | TON-Wallet-Development-Company Services"
    description="Leverage our phenomenal TON wallet development services and revolutionize the crypto world with a feature-rich, user-friendly, ultra-secure digital wallet."
    keywords="TON Wallet Development Company, TON ecosystem, TON wallet development services, TON Wallet Development, TON wallets, crypto wallet development company, crypto wallets, crypto solutions, TON blockchain, Toncoin, Tonkeeper, Tonhub, crypto traders, custom TON wallet development company, TON network, Ledger hardware wallets, TON Crypto Wallet, decentralized apps (DApps), DeFi protocols, decentralized exchanges (DEXs), crypto portfolio management, TON Wallet Development Process, crypto wallet developer,  cryptocurrency wallet development company, digital wallets, blockchain platforms, Blockchain experts"
  ></Seo>
);

export default TonWalletPage;
export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
